@import "./_job.less";
@job-img-dir: 'job/_images';

.r-bg{
	background:#fff;
	overflow:hidden;

	.record{
		width:94%;
		.margin(3%);
		border-top:1*@rex solid #D6D6D6;
		border-left:1*@rex solid #D6D6D6;
		border-bottom:1*@rex solid #D6D6D6;
		overflow:hidden;

		.r-case{
			width:100%;
			line-height:40*@rex;
			overflow:hidden;

			li{
				display:block;
				text-indent: 3*@rex;
				color:#666666;
				float:left;
				border-right:1*@rex solid #D6D6D6;
				text-align:left;
				height:40*@rex;
				overflow:hidden;
			}
			a.bule{
				color:#0066CC;
			}

			.cos-1{
				width:31%;
			}

			.cos-2{
				width:23%;
			}

			.cos-3{
				width:46%;
			}

			.text{
				text-align:center;
				display:block;
			}
		}

		.record-bg{
			background:#EEEEEE;
		}
	}
}